import { Controller, HttpCode, Logger, Post, Request, UseGuards } from '@nestjs/common'
import { UserLocalAuthGuard } from './userLocalAuth.guard'
import { UserService } from '@flowda-projects/cms-admin-services'
import { UserJwtAuthGuard } from './userJwtAuth.guard'

@Controller('user')
export class UserController {
  private readonly logger = new Logger(UserController.name)

  constructor(private service: UserService) {}

  @UseGuards(UserLocalAuthGuard)
  @Post('login')
  @HttpCode(200)
  login(@Request() req) {
    return req.user
  }

  @UseGuards(UserJwtAuthGuard)
  @Post('logout')
  logout(@Request() req) {
    // 客户端清空 token
    // 服务端清空 refresh token
    // 暂时不做 blacklist access_token 失效时间 10min
    return req.user
  }
}
