import { inject, injectable } from 'inversify'
// import * as db from '@prisma/client-wms'
import { createZodDto } from 'nestjs-zod'
import { PrismaClientSymbol } from '@flowda-projects/flowda-shared'
import { LoggerService } from '@nestjs/common'
import * as z from 'zod'

const QueryAuditSchema = z.object({
  auditType: z.string(),
  auditId: z.number(),
  pageSize: z.number(),
  current: z.number(),
})

export class QueryAuditSchemaDto extends createZodDto(QueryAuditSchema) {}

@injectable()
export class AuditService {
  private readonly logger

  constructor(
    @inject(PrismaClientSymbol) private prisma: any,
    @inject('Factory<Logger>') loggerFactory: (name: string) => LoggerService,
  ) {
    this.logger = loggerFactory(AuditService.name)
  }

  async queryAudit(dto: QueryAuditSchemaDto) {
    const [data, count] = await this.prisma.$transaction([
      this.prisma.audits.findMany({
        skip: dto.pageSize * (dto.current - 1),
        take: dto.pageSize,
        where: {
          auditType: dto.auditType,
          auditId: dto.auditId,
        },
        orderBy: {
          createdAt: 'desc',
        },
      }),
      this.prisma.audits.count({
        where: {
          auditType: dto.auditType,
          auditId: dto.auditId,
        },
      }),
    ])
    return {
      total: count,
      data,
    }
  }
}
