import { createTRPCProxyClient, httpBatchLink } from '@trpc/client'
import type { AppRouter } from '@flowda-projects/flowda-services-trpc-server'
import { CMS_ADMIN_ENV } from '@flowda-projects/cms-admin-services'

export const trpc = createTRPCProxyClient<AppRouter>({
  links: [
    httpBatchLink({
      url: `${CMS_ADMIN_ENV.FLOWDA_URL}/flowda-api/trpc`, // you should update this to use env variables
    }),
  ],
})
