import { z } from 'zod'
import { inject, injectable } from 'inversify'
import type { LoggerService } from '@nestjs/common'
import { generateSchema as zodToOpenAPI } from '@anatine/zod-openapi'
import * as _ from 'lodash'
import { PrismaZodSchemaSymbol } from '../../interfaces/types'
import type { IResourceAssociationSchema, IResourceColumnSchema, IResourceSchema } from '../../interfaces/schema'
import { toPath } from '../../utils/matchPath'

export const SUFFIX = 'ResourceSchema'

@injectable()
export class SchemaTransformer {
  private readonly logger
  private zodType?: z.AnyZodObject
  private jsonSchema: any
  private extendSchema: any
  private modelLevelSchema: any = {}
  private associations: IResourceAssociationSchema[] = []
  private columns: Partial<IResourceColumnSchema>[] = []
  private schemaName!: string

  constructor(
    @inject('Factory<Logger>') loggerFactory: (name: string) => LoggerService,
    @inject(PrismaZodSchemaSymbol) private prismaZod: any,
  ) {
    this.logger = loggerFactory(SchemaTransformer.name)
  }

  setZodType(z: z.AnyZodObject) {
    this.zodType = z
  }

  buildSchema(schemaName: string): SchemaTransformer {
    if (!this.zodType) {
      const errMsg = 'zodType is not initialized'
      this.logger.error(errMsg)
      throw new Error(errMsg)
    }
    this.schemaName = schemaName

    this.jsonSchema = zodToOpenAPI(this.zodType)
    this.modelLevelSchema = this.getModelSchema()

    const props = this.getProperties()
    this.columns = props.reduce<Partial<IResourceColumnSchema>[]>((acc, k) => {
      const jsProp = this.jsonSchema.properties[k]

      if (jsProp.virtual === 'true') {
        return acc // 不处理 virtual，目前只有 1..1 用到
      }

      if (jsProp.type === 'array') {
        if (!jsProp.model_name) {
          throw new Error(`${this.schemaName} 1..n model_name is not set`)
        }
        this.associations.push({
          foreign_key: this.getForeignKey(jsProp.foreign_key),
          model_name: jsProp.model_name,
          primary_key: jsProp.primary_key || 'id',
          name: k,
          slug: toPath(k),
          display_name: jsProp.title,
          schema_name: jsProp.model_name + SUFFIX,
        })
        return acc // 不处理 array
      }

      const c: Partial<IResourceColumnSchema> = _.omitBy<Partial<IResourceColumnSchema>>(
        _.assign(
          { name: k },
          {
            name: k,
            column_type: this.doColumnType(k),
            format: this.doFormat(k),
            display_name: this.doDisplayName(k),
            access_type: this.doAccessType(k),
            reference: jsProp.reference ? this.doRef(k) : undefined,
            validators: this.doValidators(k),
            prisma: jsProp.prisma,
          },
        ),
        _.isUndefined,
      )
      acc.push(c)
      return acc
    }, [])

    if (Array.isArray(this.extendSchema.columns)) {
      // 合并 __meta.columns 和 cols
      this.columns.forEach((c: any) => {
        const f = this.extendSchema.columns.find((c1: any) => c1.name === c.name)
        Object.assign(c, f || {})
      })
    }

    return this
  }

  toSchema(): Partial<IResourceSchema> {
    const name = this.schemaName.split(SUFFIX)[0]
    return _.omitBy<IResourceSchema>(
      {
        name: name,
        slug: toPath(name),
        prisma: this.modelLevelSchema.prisma != null ? this.modelLevelSchema.prisma : undefined,
        schema_name: this.schemaName,
        primary_key: this.modelLevelSchema.primary_key || 'id',
        custom: this.jsonSchema.custom,
        display_column: this.doDisplayColumn(this.modelLevelSchema.display_column),
        display_name: this.modelLevelSchema.display_name,
        display_primary_key:
          this.modelLevelSchema.display_primary_key == null
            ? true
            : this.modelLevelSchema.display_primary_key === 'true',
        searchable_columns: this.modelLevelSchema.searchable_columns
          ? this.modelLevelSchema.searchable_columns.split(',')
          : undefined,
        columns: this.columns,
        associations: this.associations,
        // __jsonschema: this.jsonSchema,
      },
      _.isUndefined,
    )
  }

  protected doDisplayColumn(display_column: string) {
    if (!display_column) return undefined // 默认 id
    const cols = display_column.split(',')
    if (cols.length > 1) return cols
    else return display_column
  }

  protected doRef(k: string): IResourceColumnSchema['reference'] {
    const jsProp = this.jsonSchema.properties[k]
    // console.log(jsProp)
    const t = jsProp.reference + 'Schema'
    const ref = zodToOpenAPI(this.prismaZod[t])
    const { primary_key, display_name, display_column } = ref
    const ret = {
      model_name: jsProp.reference,
      'x-relationField': jsProp['x-relationField'] || k.replace('Id', ''),
      'x-onSoftDelete': !jsProp.nullable && this.jsonSchema.required.indexOf(k) > -1 ? 'Restrict' : 'SetNull',
      primary_key,
      display_name: jsProp.display_name || display_name,
      display_column: this.doDisplayColumn(display_column),
      // foreign_key: jsProp.foreign_key,
    }
    if (jsProp['x-unique']) {
      return { ...ret, ...{ 'x-unique': true } }
    } else {
      return ret
    }
  }

  protected getProperties() {
    // 拿到最大的 columns
    const keys = Object.keys((this.zodType as z.AnyZodObject).shape)
    const properties = keys.filter(key => {
      const item = (this.zodType as z.AnyZodObject).shape[key]
      return (
        key !== '__meta' &&
        !(item instanceof z.ZodDefault || item._def.typeName === 'ZodDefault') &&
        !(item instanceof z.ZodNever || item._def.typeName === 'ZodDefault') &&
        keys.indexOf(key + 'Id') === -1 && // 忽略 product (product + 'Id' === productId)
        key !== 'isDeleted'
      )
    })
    return properties
  }

  protected getModelSchema() {
    this.checkValid()
    const _extends = this.extendSchema.extends
    const { prisma } = this.extendSchema
    if (prisma !== false && !this.prismaZod[_extends]) {
      throw new Error('no _extends')
    } else {
      if (prisma !== false) {
        return zodToOpenAPI(this.prismaZod[_extends])
      } else {
        const ret = Object.assign({ prisma: false }, _.omit(this.jsonSchema, ['type', 'properties', 'required']))
        // this.logger.debug!(ret)
        return ret
      }
    }
  }

  protected checkValid() {
    // 暂时认为必须有 __meta，简化下逻辑
    // 现在 __meta 里必须定义 extends
    // todo: support no prisma schema
    if (!this.jsonSchema.properties.__meta) {
      throw new Error('no __meta')
    }
    this.extendSchema = JSON.parse(this.jsonSchema.properties.__meta.description)
  }

  protected doDisplayName(k: string): string | undefined {
    const jsProp = this.jsonSchema.properties[k]
    if (typeof jsProp.title === 'string') {
      return jsProp.title
    } else {
      if (k === 'createdAt') {
        return '创建时间'
      }
      if (k === 'updatedAt') {
        return '更新时间'
      }
      return
    }
  }

  protected doAccessType(k: string): IResourceColumnSchema['access_type'] | undefined {
    const jsProp = this.jsonSchema.properties[k]
    if (typeof jsProp.access_type === 'string') {
      return jsProp.access_type
    } else {
      if (k === 'createdAt' || k === 'updatedAt' || k === this.modelLevelSchema.primary_key) {
        return 'read_only'
      } else {
        return
      }
    }
  }

  protected doColumnType(k: string): IResourceColumnSchema['column_type'] {
    const jsProp = this.jsonSchema.properties[k]
    if (Array.isArray(jsProp.enum)) {
      return 'tag'
    } else if (jsProp.override_type) {
      jsProp.type = jsProp.override_type
    } else if (jsProp.format === 'date-time') {
      return 'datetime'
    }

    if (jsProp.column_type) {
      return jsProp.column_type
    }

    return jsProp.reference ? 'reference' : jsProp.type
  }

  private doValidators(k: string): IResourceColumnSchema['validators'] | undefined {
    const jsProp = this.jsonSchema.properties[k]
    if (!jsProp) {
      this.logger.warn('undef key: ' + k)
      return
    }
    const validators: IResourceColumnSchema['validators'] = []
    if (['createdAt', 'updatedAt'].indexOf(k) === -1 && !jsProp.nullable && this.jsonSchema.required.indexOf(k) > -1) {
      validators.push({ required: true })
    }
    return validators.length === 0 ? undefined : validators
  }

  private doFormat(k: string): IResourceColumnSchema['format'] | undefined {
    const jsProp = this.jsonSchema.properties[k]
    if (Array.isArray(jsProp.enum)) {
      if (jsProp['x-enumNames']) {
        const enumNames = jsProp['x-enumNames'].split(',')
        return {
          select_options: jsProp.enum.map((opt: string, idx: number) => ({
            value: opt,
            label: enumNames[idx],
          })),
        }
      }
      return {
        select_options: jsProp.enum.map((opt: string) => ({
          value: opt,
          label: opt,
        })),
      }
    } else {
      return
    }
  }

  private getForeignKey(fk: string) {
    if (fk) return fk
    const schema = this.extendSchema.extends
    return _.lowerFirst(schema.split('Schema')[0]) + 'Id'
  }
}
