import { parseEnv } from 'znv'
import { z } from 'zod'

export const CMS_ADMIN_ENV = parseEnv(process.env, {
  TEST_ENV: z.string().optional(),
  DATABASE_URL: z.string().min(1),
  REFRESH_TOKEN_SECRET: z.string().min(1),
  REFRESH_TOKEN_EXPIRE: z.number().default(7 * 24 * 60 * 60), // 1 week
  ACCESS_TOKEN_SECRET: z.string().min(1),
  ACCESS_TOKEN_EXPIRE: z.number().default(60 * 60),
  FLOWDA_URL: z.string().min(1),
  COS_KEY: z.string().optional(),
  COS_ID: z.string().optional(),
  PROXY: z.string().default('n'),
})
