import { inject, injectable } from 'inversify'
import { FlowdaTrpcClientSymbol } from '@flowda-projects/flowda-shared'
import { LoggerService } from '@nestjs/common'
import { CreateTRPCProxyClient } from '@trpc/client'
import { AppRouter } from '@flowda-projects/flowda-services-trpc-server'

@injectable()
export class UserService {
  private readonly logger

  constructor(
    @inject(FlowdaTrpcClientSymbol) private flowdaTrpc: CreateTRPCProxyClient<AppRouter>,
    @inject('Factory<Logger>') loggerFactory: (name: string) => LoggerService,
  ) {
    this.logger = loggerFactory(UserService.name)
  }

  async validate(username: string, password: string) {
    this.logger.debug!(`[validate] invoke flowdaTrpc.user.validate, username: ${username}`)
    return this.flowdaTrpc.user.validate.query({
      username,
      password,
    })
  }
}
