import { z } from 'zod'
import { inject, injectable } from 'inversify'
import type { LoggerService } from '@nestjs/common'
import type { SchemaTransformer } from './schemaTransformer'
import { CustomZodSchemaSymbol, PrismaZodSchemaSymbol } from '../../interfaces/types'
import type { IResourceSchema } from '../../interfaces/schema'

@injectable()
export class SchemaService {
  private readonly logger
  private schemaCache?: Record<string, Partial<IResourceSchema>>

  constructor(
    @inject('Factory<Logger>') loggerFactory: (name: string) => LoggerService,
    @inject('Factory<SchemaTransformer>') private modelSchemaFactory: (z: z.AnyZodObject) => SchemaTransformer,
    @inject(PrismaZodSchemaSymbol) private zt: any,
    @inject(CustomZodSchemaSymbol) private czt: any,
  ) {
    this.logger = loggerFactory(SchemaService.name)
  }

  getSchema(): Record<string, Partial<IResourceSchema>> {
    console.time('generate schema')
    const schema = Object.keys(this.czt).reduce((acc: any, k) => {
      const e = this.czt[k]
      if (['ZodObject'].indexOf(e.constructor.name) > -1) {
        const transformer = this.modelSchemaFactory(e)
        acc[k] = transformer.buildSchema(k).toSchema()
      } else {
        this.logger.error('Wrong type', k)
      }
      return acc
    }, {})
    this.schemaCache = schema
    console.timeEnd('generate schema')
    return schema
  }

  async getSchemaCache(): Promise<Record<string, Partial<IResourceSchema>>> {
    if (!this.schemaCache) {
      // 重启的话内存里就没有了，可以手动重新获取下
      this.logger.log(`schemaCache is empty, getSchema again.`)
      return this.getSchema()
    } else {
      return this.schemaCache
    }
  }
}
