import { inject, injectable } from 'inversify'
import * as _ from 'radash'
import { lowerFirst } from 'lodash'
import type { LoggerService } from '@nestjs/common'
import type { PrismaSchemaService } from '../schema/prismaSchema.service'
import { PrismaClientSymbol } from '../../interfaces/types'
import { PrismaSchemaServiceSymbol } from '../../symbols'
// import * as db from '@prisma/client-wms'

/*
todo: 增加 reference_type 区分是如何做 nest
e.g. Customer#weixinProfile 和 Order#customerId 的 nest 查询有区别
 */
@injectable()
export class DataService {
  private readonly logger

  constructor(
    // todo: prisma 要不要强类型
    // @inject(PrismaClientSymbol) private prisma: db.PrismaClient,
    @inject(PrismaClientSymbol) private prisma: any,
    @inject(PrismaSchemaServiceSymbol) private prismaSchemaService: PrismaSchemaService,
    @inject('Factory<Logger>') loggerFactory: (name: string) => LoggerService,
  ) {
    this.logger = loggerFactory(DataService.name)
  }

  async get(reqUser: any, pathname: string, query: Record<string, any>) {
    this.logger.debug!(`[get] reqUser ${JSON.stringify(reqUser, null, 2)}`)
    this.logger.debug!(`get ${pathname}, query: ${JSON.stringify(query, null, 2)}`)
    const findParamRet = await this.prismaSchemaService.toFindParam(pathname, query)
    if (_.isEmpty(findParamRet)) {
      return {}
    }
    const { resource, action, param } = findParamRet
    if (action === 'findUnique') {
      const ret = await this.prisma[resource][action](param)
      if (ret.isDeleted) return {}
      return _.omit(ret, ['isDeleted']) as any
    }
    if (action === 'findMany') {
      const [data, count] = await this.prisma.$transaction([
        this.prisma[resource][action](param),
        this.prisma[resource].count({ where: param.where }),
      ])
      return {
        pagination: {
          total: count,
        },
        data,
      }
    }
  }

  async put(reqUser: any, path: string, values: any) {
    this.logger.debug!(`[put] reqUser ${JSON.stringify(reqUser, null, 2)}`)
    const updateParamRet = await this.prismaSchemaService.toUpdateParam(path, values)
    const { resource, param } = updateParamRet
    const prevRet = await this.prisma[resource].findUnique({
      where: {
        id: param.where.id,
      },
      select: _.mapValues(param.data, item => true),
    })
    this.logger.debug!(`prevRet ${JSON.stringify(prevRet, null, 2)}`)
    const auditChanges = Object.keys(param.data).reduce<Record<string, [string, string]>>((acc, k) => {
      acc[k] = [prevRet[k], param.data[k]]
      return acc
    }, {})
    const ret = await this.prisma[resource].update(param)
    const auditInfo = {
      auditId: param.where.id as number,
      auditType: resource,
      userId: JSON.stringify(reqUser['user_id'] || reqUser['uid']), // todo: 暂时兼容 java 和 node
      username: reqUser['user_name'],
      action: 'update',
      auditChanges: JSON.stringify(auditChanges),
      version: 0,
    }
    this.logger.log(`audit ${JSON.stringify(auditInfo, null, 2)}`)
    await this.prisma.audits.create({
      data: auditInfo,
    })
    return ret
  }

  async post(reqUser: any, path: string, values: any) {
    this.logger.debug!(`[post] reqUser ${JSON.stringify(reqUser, null, 2)}`)
    const createParamRet = await this.prismaSchemaService.toCreateParam(path, values)
    const { resource, param } = createParamRet

    if (createParamRet['x-unique']) {
      const ref = createParamRet['x-unique']
      const refId = values[ref.name!]
      const refModelName = ref.reference!.model_name
      const refData = await this.prisma[lowerFirst(refModelName)].findUnique({
        where: {
          id: refId,
        },
        include: {
          [resource]: true,
        },
      })
      const id = refData[resource].id
      const ret = await this.prisma[resource].update({
        where: {
          id: id,
        },
        data: {
          ...param.data,
          ...{
            isDeleted: false,
          },
        },
      })

      const auditInfo = {
        auditId: id,
        auditType: resource,
        userId: JSON.stringify(reqUser['user_id'] || reqUser['uid']), // todo: 暂时兼容 java 和 node
        username: reqUser['user_name'],
        action: 'soft_delete_revert',
        auditChanges: JSON.stringify(param.data),
        version: 0,
      }
      this.logger.log(`audit ${JSON.stringify(auditInfo, null, 2)}`)
      await this.prisma.audits.create({
        data: auditInfo,
      })
      return ret
    } else {
      const ret = await this.prisma[resource].create(param)
      const auditInfo = {
        auditId: ret.id,
        auditType: resource,
        userId: JSON.stringify(reqUser['user_id'] || reqUser['uid']), // todo: 暂时兼容 java 和 node
        username: reqUser['user_name'],
        action: 'create',
        auditChanges: JSON.stringify(param.data),
        version: 0,
      }
      this.logger.log(`audit ${JSON.stringify(auditInfo, null, 2)}`)
      await this.prisma.audits.create({
        data: auditInfo,
      })

      return ret
    }
  }

  async remove(reqUser: any, pathname: string) {
    this.logger.debug!(`[remove] reqUser ${JSON.stringify(reqUser, null, 2)}`)
    const assDelStrategy = await this.prismaSchemaService.getAssociationDeleteStrategy(pathname)
    const { resource, param } = await this.prismaSchemaService.toRemoveParam(pathname)
    for (const k of Object.keys(assDelStrategy)) {
      const ass = assDelStrategy[k]
      if (ass['x-onSoftDelete'] === 'Restrict') {
        const ret = await this.prisma[lowerFirst(k)].findMany({
          where: {
            isDeleted: false,
            [ass.name]: param.where.id,
          },
        })
        if (ret.length > 0) {
          throw new Error(`删除失败, 关联的<${ass.relatedDisplayName}>不为空`)
        }
      }
    }

    const prevRet = await this.prisma[resource].findUnique({
      where: {
        id: param.where.id,
      },
    })
    const ret = await this.prisma[resource].update(param)

    const auditInfo = {
      auditId: param.where.id,
      auditType: resource,
      userId: JSON.stringify(reqUser['user_id'] || reqUser['uid']), // todo: 暂时兼容 java 和 node
      username: reqUser['user_name'],
      action: 'soft_delete',
      auditChanges: JSON.stringify(prevRet),
      version: 0,
    }
    this.logger.log(`audit ${JSON.stringify(auditInfo, null, 2)}`)
    await this.prisma.audits.create({
      data: auditInfo,
    })
    return ret
  }
}
