import { z } from 'zod'
import { createZodDto } from 'nestjs-zod'

const GetTemplateDataDefSchema = z.object({
  siteId: z.number(),
})

export class GetTemplateDataDefSchemaDto extends createZodDto(GetTemplateDataDefSchema) {}

const GenerateSiteSchema = z.object({
  templateId: z.number(),
  customerId: z.number(),
})

export class GenerateSiteSchemaDto extends createZodDto(GenerateSiteSchema) {}

const SubmitPreviewSiteSchema = z.object({
  siteId: z.number(),
  slotData: z.any(),
})

export class SubmitPreviewSiteSchemaDto extends createZodDto(SubmitPreviewSiteSchema) {}

const GeneratePartialSlotDataSchema = z.object({
  siteId: z.number(),
  path: z.string(),
})

export class GeneratePartialSlotDataSchemaDto extends createZodDto(GeneratePartialSlotDataSchema) {}

const syncSiteUpdateToProjectSchema = z.object({
  id: z.number(),
  name: z.string(),
})

export class SyncSiteUpdateToProjectSchemaDto extends createZodDto(syncSiteUpdateToProjectSchema) {}

export const queryLinksSchema = z.object({
  projectId: z.string(),
})

export class QueryLinksSchemaDto extends createZodDto(queryLinksSchema) {}

export const queryProjectUsersSchema = z.object({
  projectId: z.string(),
})

export class QueryProjectUsersSchemaDto extends createZodDto(queryProjectUsersSchema) {}

export const updateProjectSchema = z.object({
  previousSlug: z.string(),
  slug: z.string().optional(),
  name: z.string().optional(),
})

export class UpdateProjectSchemaDto extends createZodDto(updateProjectSchema) {}

const syncProjectUpdateToSiteSchema = z.object({
  previousSlug: z.string(),
  name: z.string().optional(),
})

export class SyncProjectUpdateToSiteSchemaDto extends createZodDto(syncProjectUpdateToSiteSchema) {}
