import type { LoggerService } from '@nestjs/common'
import { inject, injectable } from 'inversify'
import { toModelName, toPath, toSchemaName } from '../../utils/matchPath'

@injectable()
export class DynamicTableSchemaTransformer {
  private readonly logger: LoggerService

  constructor(@inject('Factory<Logger>') loggerFactory: (name: string) => LoggerService) {
    this.logger = loggerFactory(DynamicTableSchemaTransformer.name)
  }

  transform(input: any) {
    const cols = input.dynamicTableDefColumns.map((c: any) => {
      return Object.assign(
        {
          name: c.name,
          column_type: c.type,
        },
        c.extendedSchema,
      )
    })
    return Object.assign(
      {
        name: toModelName(input.name),
        slug: toPath(input.name),
        schema_name: toSchemaName(input.name),
        primary_key: 'id',
        columns: cols,
        prisma: false,
        is_dynamic: true,
      },
      input.extendedSchema,
    )
  }
}
