import { ContainerModule, interfaces } from 'inversify'
import { z } from 'zod'

import {
  bindServiceSymbol,
  DataServiceSymbol,
  DynamicTableSchemaTransformer,
  DynamicTableSchemaTransformerSymbol,
  PrismaUtilsSymbol,
  SchemaServiceSymbol,
  ServiceSymbol,
} from '@flowda-projects/flowda-shared'
import { PrismaSchemaService } from './services/schema/prismaSchema.service'
import { DataService } from './services/data/data.service'
import { SchemaService } from './services/schema/schema.service'
import { SchemaTransformer } from './services/schema/schemaTransformer'
import { PrismaUtils } from './services/schema/prismaUtils'
import { PrismaSchemaServiceSymbol, SchemaTransformerSymbol } from './symbols'

export const flowdaSharedModule = new ContainerModule((bind: interfaces.Bind) => {
  bindServiceSymbol(bind, ServiceSymbol, DataServiceSymbol, DataService)
  bindServiceSymbol(bind, ServiceSymbol, SchemaServiceSymbol, SchemaService)

  bind<PrismaSchemaService>(PrismaSchemaServiceSymbol).to(PrismaSchemaService).inSingletonScope()
  bind<PrismaUtils>(PrismaUtilsSymbol).to(PrismaUtils).inSingletonScope()

  bind<SchemaTransformer>(SchemaTransformerSymbol).to(SchemaTransformer).inTransientScope()
  bind<interfaces.Factory<SchemaTransformer>>('Factory<SchemaTransformer>').toFactory<SchemaTransformer, [any]>(
    context => {
      return (z: z.AnyZodObject) => {
        const transformer = context.container.get<SchemaTransformer>(SchemaTransformerSymbol)
        transformer.setZodType(z)
        return transformer
      }
    },
  )
  bind<DynamicTableSchemaTransformer>(DynamicTableSchemaTransformerSymbol)
    .to(DynamicTableSchemaTransformer)
    .inTransientScope()
})
