import { inject, injectable } from 'inversify'
import { TrpcService } from './trpc.service'
import type { LoggerService } from '@nestjs/common'
import {
  CustomService,
  queryLinksSchema,
  queryProjectUsersSchema,
  updateProjectSchema,
} from '@flowda-projects/cms-admin-services'
import { z } from 'zod'

@injectable()
export class ProjectRouter {
  private readonly logger

  constructor(
    @inject(TrpcService) private trpc: TrpcService,
    @inject(CustomService) private customService: CustomService,
    @inject('Factory<Logger>') loggerFactory: (name: string) => LoggerService,
  ) {
    this.logger = loggerFactory(ProjectRouter.name)
  }

  projectRouter = this.trpc.router({
    queryLinks: this.trpc.procedure
      .input(queryLinksSchema)
      .output(z.any())
      .query(async ({ input }) => {
        return this.customService.queryLinks(input)
      }),

    queryProjectUsers: this.trpc.procedure
      .input(queryProjectUsersSchema)
      .output(z.any())
      .query(async ({ input }) => {
        return this.customService.queryProjectUsers(input)
      }),

    updateProject: this.trpc.procedure
      .input(updateProjectSchema)
      .output(z.any())
      .mutation(async ({ input }) => {
        this.logger.debug!('[updateProject] invoked')
        return this.customService.updateProject(input)
      }),
  })
}
