// todo: 改成 zod 定义
export interface IResourceSchema {
  prisma?: boolean
  is_dynamic?: boolean
  schema_name: string
  name: string
  slug: string
  primary_key: string
  custom?: any
  display_column?: string | string[]
  display_name: string | null
  display_primary_key: boolean
  searchable_columns?: string[]
  columns: Partial<IResourceColumnSchema>[]
  associations: IResourceAssociationSchema[]
  __jsonschema?: any
}

export interface IResourceAssociationSchema {
  foreign_key: string
  /**
   * Prisma Model 名称
   */
  model_name: string
  primary_key: string
  /**
   * Prisma 字段名称
   */
  name: string
  slug: string
  display_name: string
  schema_name: string
}

export interface ISchemaSelectOption {
  value: string | number
  label: string
}

export interface IResourceColumnSchema {
  name: string
  access_type: 'read_only'
  column_type: 'reference' | 'string' | 'tag' | 'integer' | 'datetime' | 'textarea' | 'boolean'
  prisma?: boolean
  format: {
    select_options: ISchemaSelectOption[]
  }
  reference: {
    model_name: string
    primary_key: string
    display_name: string
    display_column?: string | string[]
    'x-relationField': string
    'x-onSoftDelete': string
    'x-unique'?: boolean
    // foreign_key: jsProp.foreign_key,
  }
  display_name: string | null
  validators: Array<
    | {
        required: boolean
      }
    | {
        format: string
        message: string
      }
  >
}
