import { ContainerModule, interfaces } from 'inversify'
import { Logger, LoggerService } from '@nestjs/common'
import { bindService, ServiceSymbol } from '@flowda-projects/flowda-shared'
import { TableFilterService } from './assist/table-filter.service'
import { AuditService } from './assist/audit.service'

export const flowdaSharedNodeModule = new ContainerModule((bind: interfaces.Bind) => {
  bindService(bind, ServiceSymbol, TableFilterService)
  bindService(bind, ServiceSymbol, AuditService)
  bind<interfaces.Factory<LoggerService>>('Factory<Logger>').toFactory<LoggerService, [string]>(context => {
    return name => {
      return new Logger(name)
    }
  })
})
